.search {
 align-self: center;
 display: grid;
 justify-self: start;
 width: 100%;
 max-width: 640px;
 margin-left: 59px;

 &.focused {
  padding-left: 0;

  & .search-form .search-form-container {
   border-color: var(--dark-blue-color);

   & .search-icon {
    display: block;
   }
  }
 }

 & .search-form {
  display: grid;
  grid-template-columns: 1fr auto 40px;
  width: 100%;
  height: 40px;

  & .search-form-container {
   position: relative;
   display: flex;
   align-items: center;
   background-color: var(--dark-color);
   border: 1px solid var(--lighter-dark-color);
   border-radius: 40px 0 0 40px;

   & .search-icon {
    display: none;
    max-width: 20px;
    min-width: 20px;
    margin-left: 17px;
    fill: white;
   }

   & .search-form__virtual-keyboard-icon {
    width: 19px;
    height: 11px;
    margin-right: 10px;

    &.dark-theme {
     filter: invert(1);
    }

    &.dark-theme:hover {
     filter: invert(1) brightness(1.5);
    }
   }

   & .search-form__field {
    width: 100%;
    margin-left: 16px;
    padding: 0 4px 0 0;
    font-size: 16px;
    font-family: Roboto, Noto, sans-serif;
    font-weight: 500;
    line-height: 38px;
    color: white;
    background-color: var(--dark-color);
   }
  }

  & .search-icon-button {
   position: relative;
   width: 63px;
   border: 1px solid var(--lighter-dark-color);
   border-left: 0;
   border-radius: 0 40px 40px 0;
   background-color: var(--light-dark-color);
   fill: white;

   &:hover {
    & .text-tip {
     display: block;
    }
   }
  }
 }

 & .voice-search-button {
  margin-left: 16px;
 }
}

@media (max-width: 750px) {
 .voice-search-button.mini {
  & .text-tip {
   right: 170% !important;
  }
 }

 .search {
  & .search-form {
   padding-left: 32px;
  }
 }
}
