.create-video-actions-modal {
 position: absolute;
 top: 100%;
 right: 0;
 z-index: 300;
 display: none;
 padding: 8px 0;
 border-radius: 12px;
 background-color: var(--light-dark-color);

 &.opened {
  display: block;
 }

 & .create-video-actions-modal__item {
  display: flex;
  align-items: center;
  padding: 0 36px 0 16px;
  font-size: 14px;

  &:hover {
   background-color: var(--dark-gray-color);
  }

  & .item-title {
   height: 100%;
   margin-left: 16px;
   padding-right: 4px;
   line-height: 40px;
   user-select: none;
   white-space: nowrap;
  }
 }
}
