@import './variables';
@import './normalize';
@import './list';
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oswald&family=Roboto:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');

.app {
 font-family: Roboto, Arial, sans-serif;
 color: white;
 background-color: var(--primary-color);
 overflow-y: auto;
 overflow-x: hidden;

 &::-webkit-scrollbar {
  background-color: transparent;
  width: 16px;
 }

 &::-webkit-scrollbar-track {
  display: none;
  margin: 5px;
  background-color: transparent;
 }

 &::-webkit-scrollbar-thumb {
  min-height: 240px; //TODO Cant find right value because browser overwriting it
  border: solid 4px transparent;
  border-radius: 14px;
  box-shadow: inset 0 0 14px 14px var(--light-gray-color);

  &:hover {
   box-shadow: inset 0 0 14px 14px var(--gray-color);
  }
 }
 &::-webkit-scrollbar-button {
  display: none;
 }
}
